footer {
  padding: 28px 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  color: lightgray;

  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
    font-size: 16px;
    line-height: 1.19;
  }

  @media (min-width: 1280px) {
    padding: 40px 0;
  }
}

.footer-heart-icon {
  margin: 0 10px;
  fill: orange;
}
//temporary
.footer-heart-icon:hover {
  stroke: none;
}
//_______________
.goit-students {
  text-decoration: underline;
}
.footer-copyright {
  margin-right: 7px;
}
.footer-wrapper.first-string {
  display: flex;
  column-gap: 5px;
}

.footer-students {
  //   position: absolute;
  //   top: 50%;
  //   left: 50%;

  //   transform: translate(-50%, -50%);
  width: 280px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 16px;
  margin: 0 auto;
  background-color: black;
  outline: 10px solid black;
  border-radius: 5px;
  @media (min-width: 768px) {
    width: 704px;
    column-gap: 16px;
    row-gap: 28px;
  }

  @media (min-width: 1280px) {
    row-gap: 32px;
    width: 1217px;
  }
}

.students-overlay {
/*   Если хотите попробовать дописать сотрите эту строку  */

  display: none;


  
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 100vh;
  padding-top: 40px;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  background: #000c;
  overflow-y: scroll;
  overflow-x: hidden;
}
.students-header {
  margin-bottom: 20px;
  font-size: 28px;
  line-height: 33px;
  text-transform: uppercase;
  text-align: center;
}
.footer-students .film-card__genre {
  max-width: none;
}

.students-modal-close-btn {
  position: absolute;
  top: 48px;
  right: 48px;
}
.students-overlay audio {
  position: absolute;
  z-index: 999;
}


.students-modal-close-btn {
  position: absolute;
  top: 48px;
  right: 48px;
  background-image: url(../image/close-btn.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
